* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-info {
  display: inline-block;
  font: inherit;
  padding: 8px;
}

.country-container {
  display: flex;
}

.country-img {
  object-fit: contain;
  width: 90px;
  height: auto;
}

.country-text {
  margin: 0;
  margin-left: 20px;
  font-size: 50px;
}
